"use client";

import * as React from "react";
import {
  AlertDialog,
  AlertDialogTrigger,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogFooter,
} from "@/components/ui/alert-dialog";
import { Button } from "@/components/ui/button";

interface DeleteOrderDialogProps {
  onDelete: () => void;
}

export const DeleteOrderDialog: React.FC<DeleteOrderDialogProps> = ({ onDelete }) => {
  return (
    <AlertDialog>
      <AlertDialogTrigger asChild>
        <Button variant="destructive" size="sm">
          Delete
        </Button>
      </AlertDialogTrigger>
      <AlertDialogContent>
        <AlertDialogHeader>
          Are you sure you want to delete this order?
        </AlertDialogHeader>
        <AlertDialogFooter className="flex justify-end gap-2">
          <Button variant="outline">Cancel</Button>
          <Button
            variant="destructive"
            onClick={() => {
              onDelete();
            }}
          >
            Delete
          </Button>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
};
