import { ColumnDef } from "@tanstack/react-table";
import { Order } from "./types";
import { Button } from "@/components/ui/button";
import { DeleteOrderDialog } from "./components/delete-order-dialog";
import { deleteOrder } from "./api";
import { toast } from "sonner";

export const columns: ColumnDef<Order>[] = [
  { accessorKey: "orderNo", header: "Order No" },
  { accessorKey: "customerName", header: "Customer" },
  { accessorKey: "status", header: "Status" },
  { accessorKey: "amount", header: "Amount" },
  { accessorKey: "createdAt", header: "Created At" },
  {
    id: "actions",
    header: "Actions",
    cell: ({ row }) => (
      <div className="flex gap-2">
        {/* Edit Button (later can open Edit dialog) */}
        <Button size="sm">Edit</Button>

        {/* Delete Button with Dialog */}
        <DeleteOrderDialog
          onDelete={async () => {
            try {
              await deleteOrder(row.original.id); // call API
              toast.success("Order deleted successfully");
              // Optionally: refresh table data by refetching
            } catch (error) {
              toast.error("Failed to delete order");
            }
          }}
        />
      </div>
    ),
  },
];



